import React,{Component}  from 'react';
import   {connect} from 'react-redux'
import Input from '../../components/UI/Input/Input'
import Button from '../../components/UI/Button/Button';
import classes from './Auth.css'
import * as action from '../../store/Actions/index'
import Spinner from '../../components/UI/Spinner/Spinner';

import {Redirect} from 'react-router-dom'; 

class Auth extends Component{

     state={
         controls:{
            email: {
                elementType: 'input',
                elementConfig: {
                    type: 'email',
                    placeholder: 'Email Address'
                },
                value: '',
                validation: {
                    required: true,
                    isEmail:true,
                },
                valid: false,
                touched: false
            },
            password: {
                elementType: 'input',
                elementConfig: {
                    type: 'password',
                    placeholder: 'Password'
                },
                value: '',
                validation: {
                    required: true,
                    minLength:7
                },
                valid: false,
                touched: false
            },
         },
         isSignup:true
     }
     checkValidity(value, rules) {
        let isValid = true;
        if (!rules) {
            return true;
        }
        
        if (rules.required) {
            isValid = value.trim() !== '' && isValid;
        }

        if (rules.minLength) {
            isValid = value.length >= rules.minLength && isValid
        }

        if (rules.maxLength) {
            isValid = value.length <= rules.maxLength && isValid
        }

        if (rules.isEmail) {
            const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
            isValid = pattern.test(value) && isValid
        }

        if (rules.isNumeric) {
            const pattern = /^\d+$/;
            isValid = pattern.test(value) && isValid
        }

        return isValid;
    }


    componentDidMount(){
        if(!this.props.buildingburger && this.props.path!=='/')
        {
            this.props.onRedirectPath();
        }
    }
    inputChangedHandler = (event, controlName) => {
        const updatedControls = {
            ...this.state.controls,
            [controlName]:{
                ...this.state.controls[controlName],
                value:event.target.value,
                valid:this.checkValidity(event.target.value,this.state.controls[controlName].validation),
                touched:true,
            }

        }
        this.setState({controls:updatedControls});
        
    }
    onSubmitHandler=(event)=>{
        event.preventDefault();
        this.props.onAuth(this.state.controls.email.value,this.state.controls.password.value,this.state.isSignup);

    }

    signUpHandler=()=>{
        this.setState(prevState=>{
            return {isSignup:!prevState.isSignup}
        })
    }

     
    render(){
        const formElementsArray = [];
        for (let key in this.state.controls) {
            formElementsArray.push({
                id: key,
                config: this.state.controls[key]
            });
        }
       
        let form = (
                formElementsArray.map(formElement => (
                    <Input 
                        key={formElement.id}
                        elementType={formElement.config.elementType}
                        elementConfig={formElement.config.elementConfig}
                        value={formElement.config.value}
                        invalid={!formElement.config.valid}
                        shouldValidate={formElement.config.validation}
                        touched={formElement.config.touched}
                        changed={(event) => this.inputChangedHandler(event, formElement.id)} />
                ))
                
        );
        if(this.props.loading)
            form=<Spinner/>

        let errorMessege=null;
        if(this.props.error)
        {
             errorMessege=(
             <p>{this.props.error.message}</p>
             );
        }
             
        let isauthentiacton=null
          if(this.props.isauthenticated)
             {
                 isauthentiacton=<Redirect to={this.props.path}/>
             }

        return(
            <div className={classes.Auth}>
                {isauthentiacton}
                {errorMessege}
                
                   <form onSubmit={this.onSubmitHandler}>
                       {form}
                   <Button btnType="Success"  >SUBMIT</Button>
                   </form>
                    <Button btnType="Success"  clicked={this.signUpHandler}>SWITCH TO {this.state.isSignup?"SIGNIN":"SIGNUP"}</Button>
            </div>
       )
    }
}
const stateDiapatchToprops=state=>{
       return{
            loading:state.auth.loading,
            error:state.auth.error,
            isauthenticated:state.auth.token!==null ,
            buildingburger:state.burger.building,
            path:state.auth.path           
       }
}


const mapDispatchToProps=dispatch=>{
    return{
        onAuth:(email,password,isSignup)=>dispatch(action.auth(email,password,isSignup)),
        onRedirectPath:()=>dispatch(action.authRedirect('/')) 
    }
}

export default connect(stateDiapatchToprops,mapDispatchToProps)(Auth);


