import * as actionTypes from '../Actions/actionTypes';
import axios from 'axios'

export const  authLogout=()=>{
    localStorage.removeItem('token');
    localStorage.removeItem('ExpirationTime');
    localStorage.removeItem('id');

    return{
        type:actionTypes.AUTH_LOGOUT, 
    }

}

export const checkAuthTimeout=(expiretime)=>{   
   return dispatch=>{
       setInterval(() => {
           dispatch(authLogout())
       },expiretime*1000);
   }

} 

export const  authStart=()=>{

    return{
        type:actionTypes.AUTH_START, 
    }

}


export const authSuccess=(token,userId)=>{
    return{
        type:actionTypes.AUTH_SUCCESS,
        token:token,
        userId:userId
        
    }  ; 
};
export const authFail=(error)=>{
    return{
        type:actionTypes.AUTH_FAIL,
        error:error
        
    }   
}
export const auth=(email,password,isSignup)=>{

    return dispatch=>{
       dispatch(authStart());
       const authData={
            email:email,
            password:password,
            returnSecureToken:true
       }

       let url='https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyD9KAFBOgLyTtR37WnM94_uFB501ltXbOw';
        if(!isSignup)
        url='https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyD9KAFBOgLyTtR37WnM94_uFB501ltXbOw';

        axios.post(url,authData)
        .then(response=>{
            console.log(response)
            const expretime= new Date(new Date().getTime()+response.data.expiresIn*1000);

            localStorage.setItem('token',response.data.idToken);
            localStorage.setItem('ExpirationTime',expretime);
            localStorage.setItem('id',response.data.localId);
            dispatch(authSuccess(response.data.idToken,response.data.localId))
            dispatch(checkAuthTimeout(response.data.expiresIn))
        })
        .catch(error=>{
            console.log(error)
            dispatch(authFail(error.response.data.error))
        })
    }   
}
export const authRedirect=(path)=>{
    return{
        type:actionTypes.AUTH_REDIRECT_PATH,
        path:path
        
    }   
}

export const authCheckState=()=>{
    return dispatch=>{
        const token=localStorage.getItem('token');
        if(!token)
        {
            dispatch(authLogout());
        }
        else
        {
            const expireDate=new Date(localStorage.getItem('ExpirationTime'))
            if(expireDate<=new Date()){
                dispatch(authLogout());
            }
            else{
                 const userId=localStorage.getItem('id');
                 console.log(userId)
                 dispatch(authSuccess(token,userId));
                 dispatch(checkAuthTimeout((expireDate.getTime()-new Date().getTime())/1000));
            }
        }
    }
} 


