import * as actionTypes from './actionTypes';

import axios from '../../axios-orders';

export const purchaseBurgerSuccess=(id,data)=>{
      return{
          type:actionTypes.PURCHASE_BURGER_SUCCESS,
          orderId:id,
          orderData:data
      }

}


export const purchaseBurgerFail=(error)=>{
    return{
        type:actionTypes.PURCHASE_BURGER_FAIL,
        error:error
        
    }
}
export const purchaseBurgerStart=()=>{
   return{
       type:actionTypes.PURCHASE_BURGER_START
   }
}

export const purchaseBurger=(orderData,token)=>{

     
    return  dispatch =>{
        dispatch(purchaseBurgerStart());
        axios.post( '/orders.json?auth='+token, orderData )
        .then( response => {
             dispatch(purchaseBurgerSuccess(response.data.name,orderData))
        } )
        .catch( error => {
            dispatch(purchaseBurgerFail(error))
        } );
    }
}
export const purchaseInit=()=>{
    return{
        type:actionTypes.PURCHASE_INIT 
    }
 }

 export const orderFetchStart=()=>{
    return{
        type:actionTypes.ORDER_FETCH_START
    }
}
export const orderFetchSuccess=(orders)=>{
    return{
        type:actionTypes.ORDER_FETCH_SUCCESS,
        orders:orders
    }
}
export const orderFetchFail=(error)=>{
    return{
        type:actionTypes.ORDER_FETCH_START,
        error:error
        
    }
}
export const fetchOrder=(token,userId)=>{
    return dispatch=>{
           dispatch(orderFetchStart())
  
           const queryParams='?auth='+token+'&orderBy="userId"&equalTo="'+userId+'"';
           axios.get('/orders.json'+ queryParams)
            .then(res => {
                const fetchedOrders = [];
                for (let key in res.data) {
                    fetchedOrders.push({
                        ...res.data[key],
                        id: key
                    });
                }
               dispatch(orderFetchSuccess(fetchedOrders));
            })
            .catch(err => {
                dispatch(orderFetchFail(err));
                
            });
    }
}



