import * as actionstype from '../Actions/actionTypes';
import {updatedObject } from '../Utility'

const intialState={
    token:null,
    userId:null,
    error:null,
    loading:false,
    path:"/"
};


const authStart=(state,action)=>{
    return updatedObject(state,{error:null,loading:true});
}

const authSuccess=(state,action)=>{

      console.log(action.userId)
    return updatedObject(state,{ 
        userId:action.userId,
        token:action.token,
        error:null,
        loading:false
    });
}
const authFail=(state,action)=>{
    return updatedObject(state,{
        error:action.error,
        loading:false
    }
  )
}
const authLogout=(state,action)=>{
    return updatedObject(state,{token:null,userId:null})
}
const authRedirect=(state,action)=>{
    return updatedObject(state,{path:action.path})
}


const reducer=(state=intialState,action)=>{
    switch(action.type){
        case actionstype.AUTH_START:
            return authStart(state,action)
        case actionstype.AUTH_SUCCESS:
            return authSuccess(state,action)
        case actionstype.AUTH_FAIL:
            return authFail(state,action) 
        case actionstype.AUTH_LOGOUT:
            return authLogout(state,action) 
        case actionstype.AUTH_REDIRECT_PATH:
            return authRedirect(state,action)              
        default:
        return state    
    }
}
export default reducer;


