import React, { Component } from 'react';

import Aux from '../../hoc/Aux/Aux';
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary';
import Spinner from '../../components/UI/Spinner/Spinner';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import axios from '../../axios-orders';

import {connect} from 'react-redux';
import * as burgerBuilderActions from '../../store/Actions/index';
//import thunk from 'redux-thunk';



class BurgerBuilder extends Component {
    // constructor(props) {
    //     super(props);
    //     this.state = {...}
    // }
    state = {
        purchasing: false, 
    }

    componentDidMount () {
        console.log(this.props);
       this.props.onInitIngredients();
       
    }

    updatePurchaseState ( ingredients ) {
        const sum = Object.keys( ingredients )
            .map( igKey => {
                return ingredients[igKey];
            } )
            .reduce( ( sum, el ) => {
                return sum + el;
            }, 0 );
        return sum>0;
    }

   
    purchaseHandler = () => {
        if(this.props.isAuthenticated)
        {
            this.setState( { purchasing: true } );
        }
        else{
            this.props.onAuthRedirect('/checkout');
            this.props.history.push('/auth')
        }
        
    }

    purchaseCancelHandler = () => {
        this.setState( { purchasing: false } );
    }

    purchaseContinueHandler = () => {
        // alert('You continue!');
       this.props.onInitPurchase();
        this.props.history.push('/checkout');
    }

    render () {
        const disabledInfo = {
            ...this.props.ings
        };
        for ( let key in disabledInfo ) {
            disabledInfo[key] = disabledInfo[key] <= 0
        }
        let orderSummary = null;
        let burger = this.props.error ? <p>Ingredients can't be loaded!</p> : <Spinner />;

        if ( this.props.ings ) {
            burger = (
                <Aux>
                    <Burger ingredients={this.props.ings} />
                    <BuildControls
                        ingredientAdded={this.props.onAddIngredients}
                        ingredientRemoved={this.props.onRemoveIngredients}
                        disabled={disabledInfo}
                        purchasable={this.updatePurchaseState(this.props.ings)}
                        ordered={this.purchaseHandler}
                        isAuth={this.props.isAuthenticated}
                        price={this.props.price} />
                </Aux>
            );
            orderSummary = <OrderSummary
                ingredients={this.props.ings}
                price={this.props.price}
                purchaseCancelled={this.purchaseCancelHandler}
                purchaseContinued={this.purchaseContinueHandler} />;
        }
        
        // {salad: true, meat: false, ...}
        return (
            <Aux>
                <Modal show={this.state.purchasing} modalClosed={this.purchaseCancelHandler}>
                    {orderSummary}
                </Modal>
                {burger}
            </Aux>
        );
    }
}

const stateToProps=state=>{
    return{
    ings:state.burger.ingredients,
    price:state.burger.totalPrice,
    error:state.burger.error,
    isAuthenticated:state.auth.token!==null
    }
}

const stateToDispatch=dispatch=>{
    return{
           onAddIngredients:(igName)=>dispatch(burgerBuilderActions.addIngredient(igName)),
           onRemoveIngredients:(igName)=>dispatch(burgerBuilderActions.removeIngredient(igName)) ,
           onInitIngredients:()=>dispatch(burgerBuilderActions.initIngredients()),
           onInitPurchase:()=>dispatch(burgerBuilderActions.purchaseInit()),
           onAuthRedirect:(path)=>dispatch(burgerBuilderActions.authRedirect(path))     
    }
}



export default   connect(stateToProps,stateToDispatch)  (withErrorHandler( BurgerBuilder, axios ));


